//main:../main.scss
.search {
  display: flex;
  padding: 15px 0 15px 80px;
  background: $clr-form;
  &__label {
    margin: 0 20px;
    cursor: pointer;
    &--hidden {
      display: none;
    }
  }
  &__input {
    width: 100%;
    border:  none;
    background: inherit;
    @include font-style($m);
  }
}
.explore {
  background: $clr-bg-sec;
  text-align: center;
  &__heading {
    padding: 0 55px;
    @include font-style($xl, $bold);
  }
  &__p {
    margin: 15px 0 20px 0;
    @include font-style($l, $light);
  }
}
